'use strict'

const test = require('ava')
//generar pruebas ficticias
const sinon = require('sinon')
//reemplazar el require por unas rutas ficticias
const proxyquire = require('proxyquire')

const agentFixtures = require('./fixtures/agent')

let config = {
  logging () {}
}

let MetricStub = {
    //cuantas veces fe usada la funcion con sinon.spy
    belongsTo: sinon.spy()
}
//clonamos el objeto single
let single = Object.assign({}, agentFixtures.single)
let id = 1 
let uuid = 'yyy-yyy-yyy'
let AgentStub = null
let db = null
//sinons que se reinician
let sandbox = null

let uuidArgs = {
  where : {uuid}
}

let newAgent = {
  uuid: '123-123-123',
  name: 'test',
  username: 'test',
  hostname: 'test',
  pid: 0,
  connected: false
}

test.beforeEach(async () => {
  //prueba que funciona dentro de un scope y se reinicia
  sandbox = sinon.createSandbox()

  AgentStub = {
    hasMany: sandbox.spy()
  }

  //Model create stub
  AgentStub.create = sandbox.stub()
  AgentStub.create.withArgs(newAgent).returns(Promise.resolve({
    toJSON () {return newAgent}
  }))

  //Model update Stub
  AgentStub.update= sandbox.stub()
  AgentStub.update.withArgs(single,uuidArgs).returns(Promise.resolve(single))

  //Model findById Stub
  AgentStub.findById = sandbox.stub()
  AgentStub.findById.withArgs(id).returns(Promise.resolve(agentFixtures.byId(id)))

  // Model findOne Stub
  AgentStub.findOne= sandbox.stub()
  AgentStub.findOne.withArgs(uuidArgs).returns(Promise.resolve(agentFixtures.byUuid(uuid)))

  //model findAll Stub
AgentStub.findAll= sandbox.stub()
AgentStub.findAll.withArgs().returns(Promise.resolve(agentFixtures.all))
AgentStub.findAll.withArgs(connectedArgs).returns(Promise.resolve(agentFixtures.connected))
AgentStub.findAll.withArgs(usernameArgs).returns(Promise.resolve(agentFixtures.platzi))


  const setupDatabase = proxyquire('../', {
    './models/agent': () => AgentStub,
    './models/metric': () => MetricStub
  })
  // la base de datos recibe el config
  db = await setupDatabase(config)
})

//reiniciar los sandbox y sinon por text
test.afterEach(() => {
  sandbox && sinon.resetHistory()
})

// test la base de datos recibe un agente
test('Agent', t => {
  t.truthy(db.Agent, 'Agent service should exist')
})

//test en serie no paralelo
test.serial('Setup', t => {
  t.true(AgentStub.hasMany.called, 'AgentModel.hasMany was executed')
  t.true(AgentStub.hasMany.calledWith(MetricStub), 'Argument should be the MetricModel')
  t.true(MetricStub.belongsTo.called, 'MetricModel.belongsTo was executed')
  t.true(MetricStub.belongsTo.calledWith(AgentStub), 'Argument should be the AgentModel')
})

test.serial('Agent#findById', async t =>{
    let agent = await db.Agent.findById(id)
    t.true(AgentStub.findById.called, 'findById should be called on model')
    t.true(AgentStub.findById.calledOnce, 'findById Should be called once')
    t.true(AgentStub.findById.calledWith(id), 'findById should be called with specific id')

    t.deepEqual(agent, agentFixtures.byId(id), 'should be the same')
})

test.serial('Agent#createOrupdate-exists', async t=>{
  let agent = await db.Agent.createOrUpdate(single)

  t.true(AgentStub.findOne.called,'findOne should be called on model')
  t.true(AgentStub.findOne.calledTwice,'findOne should be called twice')
  t.true(AgentStub.findOne.calledWith(uuidArgs),'findOne should be called with uuid args')
  t.true(AgentStub.update.called, 'agent.update called on model')
  t.true(AgentStub.update.calledOnce, 'agent.update should be called once')
  t.true(AgentStub.update.calledWith(single), 'agent.update should be called with specified args')

  t.deepEqual(agent, single, 'agent Should be the same')
})

test.serial('Agent#creteOrUpdate - new', async t=>{
  let agent = await db.Agent.createOrUpdate(newAgent)

  t.true(AgentStub.findOne.called, 'findOne should be called on model')
  t.true(AgentStub.findOne.calledOnce, 'findOne should be called once')
  t.true(AgentStub.findOne.calledWith({where: { uuid: newAgent.uuid }}), 'findOne should be called with uuid args')
  t.true(AgentStub.create.called, 'create should be called on model')
  t.true(AgentStub.create.calledOnce, 'create should be called once')
  t.true(AgentStub.create.calledWith(newAgent), 'create should be called with specified args')

  t.deepEqual(agent, newAgent, 'AgentShould be the same')
})